*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class], button {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul[class], ol[class], figure, blockquote, dl, dd, button {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizespeed;
}

ul[class], ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.main {
  padding: 50px;
  position: relative;
}

.main__slider.slider-container {
  width: 800px;
  height: 500px;
  background-color: #32323d;
  margin: 0 auto;
  box-shadow: 15px 5px 5px 7px #1e1e1e;
}

.in-column {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: flex;
}

.in-column > * {
  min-height: 1px;
  flex: 1 0;
}

.in-column > img {
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.in-row {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.in-row > * {
  min-width: 1px;
  flex: 1 0;
}

.in-row > img {
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.slide > * {
  padding: 50px;
}

.slide > img {
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.slide-dot-container {
  height: 20px;
}

.slide-number {
  color: #fff;
  font-size: 30px;
  position: absolute;
  bottom: 1rem;
  left: 50px;
  transform: translateY(-50%);
}

.slide-dot {
  cursor: pointer;
  width: 20px;
  height: 100%;
  opacity: .8;
  color: #fff;
  background: none;
  border: none;
  flex: 1 0;
  place-content: center;
  font-size: 2rem;
  display: flex;
  position: relative;
}

.slide-dot:before {
  content: "•";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slide-dot-text {
  display: none;
}

.slide-dot:hover {
  opacity: 1;
}

.slide-dot--active {
  transform: scale(1.5);
}

#btn-next, #btn-prev {
  cursor: pointer;
  opacity: .8;
  color: #fff;
  background: none;
  border: none;
  padding: 10px;
  font-size: 30px;
}

#btn-next:hover, #btn-prev:hover {
  opacity: 1;
}

#btn-prev:before {
  content: "◀︎";
}

#btn-next:before {
  content: "▶";
}

#btn-next .btn-next__text, #btn-prev .btn-prev__text {
  display: none;
}

#btn-next:focus, #btn-prev:focus, .slide-dot {
  outline: none;
}

body {
  background-color: #292930;
}

.picture-wrapper picture {
  height: 100%;
  width: 100%;
}

.picture-wrapper picture img {
  object-fit: contain;
  height: 100%;
  width: 100%;
}

